.check--active {
  background: brown;
}

.page__ul {
  display: flex;
  justify-content: flex-end;
}

li {
  list-style-type: none;
}

.hello {
  background: white;
  border: 1px solid blanchedalmond;
}

.adress-delivery__header {
  margin: 0 20px;
  grid-template-columns: auto auto 1fr;
  column-gap: 10px;
  align-items: center;
}

.adress-delivery__destination-details {
  appearance: none;
  height: 40px;
  padding: 0 12px;
  border: 1px solid #e0e0e0;
  cursor: pointer;
}

.page-item {
  list-style: none;
}

.product {
  width: 500px;
}

.product__img {
  object-fit: cover;
  width: 100%;
  display: grid;
  margin-bottom: 16px;
}

.main__container {
  display: grid;
  background: rgb(46, 71, 182);
  border: 3px solid black;
}

.information {
  display: grid;
}

.container {
  justify-content: space-between;
  display: flex;
}

button {
  width: 60px;
  height: 60px;
  border-radius: 40%;
  cursor: pointer;
}

.page-link {
  text-decoration: none;
  font-size: 20px;
}
